"use client";

import React, { useEffect, useRef, useState } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";

const SimulatedVideo = ({ onFrame }: { onFrame: (imageData: string) => void }) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [hue, setHue] = useState(0);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const animate = () => {
      ctx.fillStyle = `hsl(${hue}, 70%, 60%)`;
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      
      // Draw a simple face shape
      ctx.beginPath();
      ctx.arc(canvas.width / 2, canvas.height / 2, 50, 0, Math.PI * 2);
      ctx.strokeStyle = "white";
      ctx.lineWidth = 3;
      ctx.stroke();

      // Eyes
      ctx.beginPath();
      ctx.arc(canvas.width / 2 - 20, canvas.height / 2 - 10, 5, 0, Math.PI * 2);
      ctx.arc(canvas.width / 2 + 20, canvas.height / 2 - 10, 5, 0, Math.PI * 2);
      ctx.fillStyle = "white";
      ctx.fill();

      // Mouth
      ctx.beginPath();
      ctx.arc(canvas.width / 2, canvas.height / 2 + 10, 20, 0, Math.PI);
      ctx.stroke();

      setHue((h) => (h + 1) % 360);
      onFrame(canvas.toDataURL("image/png"));
    };

    const interval = setInterval(animate, 100);
    return () => clearInterval(interval);
  }, [hue, onFrame]);

  return (
    <canvas
      ref={canvasRef}
      width={640}
      height={480}
      className="w-full h-full rounded-lg"
    />
  );
};

export default function CameraFeed() {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [capturedImage, setCapturedImage] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [simulatedMode, setSimulatedMode] = useState(true);
  const [currentFrame, setCurrentFrame] = useState<string | null>(null);
  const [videoStream, setVideoStream] = useState<MediaStream | null>(null);
  const [isCameraSupported, setIsCameraSupported] = useState(true);
  const [permissionStatus, setPermissionStatus] = useState<string>("prompt");

  // Check camera support and permissions on mount
  useEffect(() => {
    const checkCameraSupport = async () => {
      if (!navigator.mediaDevices?.getUserMedia) {
        setIsCameraSupported(false);
        setError("Camera is not supported in this browser");
        return;
      }

      try {
        // Try to get camera permission status
        const result = await navigator.permissions.query({ name: 'camera' as PermissionName });
        setPermissionStatus(result.state);
        
        result.addEventListener('change', () => {
          setPermissionStatus(result.state);
          if (result.state === 'denied') {
            stopLiveCamera();
            setSimulatedMode(true);
          }
        });
      } catch (err) {
        // Some browsers might not support the permissions API
        console.log('Permission check not supported, will check when accessing camera');
      }
    };

    checkCameraSupport();
  }, []);

  const startLiveCamera = async () => {
    try {
      if (!navigator.mediaDevices?.getUserMedia) {
        throw new Error("Camera is not supported in this browser");
      }

      // Stop any existing stream first
      stopLiveCamera();

      console.log("Requesting camera access...");
      const stream = await navigator.mediaDevices.getUserMedia({
        video: {
          facingMode: "user",
          width: { ideal: 640 },
          height: { ideal: 480 }
        }
      });
      
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        try {
          // Wait for metadata to be loaded
          await new Promise<void>((resolve) => {
            if (videoRef.current) {
              videoRef.current.onloadedmetadata = () => resolve();
            }
          });
          await videoRef.current.play();
          console.log("Video stream started successfully");
        } catch (playError) {
          console.error("Error playing video:", playError);
          throw new Error("Failed to start video stream");
        }
      }
      
      setVideoStream(stream);
      setError(null);
      setPermissionStatus('granted');
    } catch (err: any) {
      console.error("Camera error:", err);
      if (err.name === 'NotAllowedError' || err.name === 'PermissionDeniedError') {
        setError("Camera access denied. Please allow camera access in your browser settings.");
        setPermissionStatus('denied');
      } else {
        setError(err.message || "Unable to access the camera. Please check your device settings.");
      }
      setSimulatedMode(true);
    }
  };

  const stopLiveCamera = () => {
    if (videoStream) {
      videoStream.getTracks().forEach(track => {
        track.stop();
        console.log("Stopped video track:", track.label);
      });
      setVideoStream(null);
    }
    if (videoRef.current) {
      videoRef.current.srcObject = null;
    }
  };

  useEffect(() => {
    if (!simulatedMode) {
      startLiveCamera();
    } else {
      stopLiveCamera();
    }

    return () => {
      stopLiveCamera();
    };
  }, [simulatedMode]);

  const captureImage = () => {
    try {
      if (simulatedMode) {
        if (currentFrame) {
          setCapturedImage(currentFrame);
        }
      } else {
        const video = videoRef.current;
        if (!video) {
          throw new Error("Video element not found");
        }

        const canvas = document.createElement("canvas");
        canvas.width = video.videoWidth || 640;
        canvas.height = video.videoHeight || 480;
        
        const ctx = canvas.getContext("2d");
        if (!ctx) {
          throw new Error("Could not get canvas context");
        }

        ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
        const dataUrl = canvas.toDataURL("image/png");
        setCapturedImage(dataUrl);
      }
      setError(null);
    } catch (err: any) {
      setError("Failed to capture image: " + err.message);
    }
  };

  const submitAttendance = async () => {
    if (!capturedImage) return;

    setIsSubmitting(true);
    try {
      const response = await fetch('/api/attendance', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          image: capturedImage,
          name: 'Test User'
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to submit attendance');
      }

      setCapturedImage(null);
      setError(null);
    } catch (err) {
      setError('Failed to submit attendance. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  const retake = () => {
    setCapturedImage(null);
    setError(null);
  };

  return (
    <Card className="p-6">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-2xl font-semibold">Camera Feed</h2>
        <div className="flex items-center space-x-4">
          <div className="flex items-center space-x-2">
            <Switch 
              id="camera-mode"
              checked={!simulatedMode}
              onCheckedChange={(checked) => setSimulatedMode(!checked)}
              disabled={!isCameraSupported || permissionStatus === 'denied'}
            />
            <Label htmlFor="camera-mode" className="text-sm text-gray-600">
              {simulatedMode ? "Simulation" : "Live Camera"}
            </Label>
          </div>
        </div>
      </div>

      {permissionStatus === 'denied' && (
        <Alert variant="destructive" className="mb-4">
          <AlertDescription>
            Camera access is blocked. Please allow camera access in your browser settings to use live camera mode.
          </AlertDescription>
        </Alert>
      )}

      <div className="relative aspect-video bg-gray-100 rounded-lg mb-4">
        {!capturedImage ? (
          simulatedMode ? (
            <SimulatedVideo onFrame={setCurrentFrame} />
          ) : (
            <video
              ref={videoRef}
              autoPlay
              playsInline
              muted
              className="w-full h-full object-cover rounded-lg"
            />
          )
        ) : (
          <img
            src={capturedImage}
            alt="Captured"
            className="w-full h-full object-cover rounded-lg"
          />
        )}
      </div>

      <div className="flex gap-4">
        {!capturedImage ? (
          <Button 
            onClick={captureImage} 
            className="w-full"
          >
            Capture
          </Button>
        ) : (
          <>
            <Button 
              onClick={retake} 
              variant="outline" 
              className="w-full"
              disabled={isSubmitting}
            >
              Retake
            </Button>
            <Button 
              onClick={submitAttendance} 
              className="w-full"
              disabled={isSubmitting}
            >
              {isSubmitting ? 'Submitting...' : 'Submit Attendance'}
            </Button>
          </>
        )}
      </div>

      {error && (
        <Alert variant="destructive" className="mt-4">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {simulatedMode && (
        <p className="mt-4 text-sm text-gray-500 text-center">
          {isCameraSupported && permissionStatus !== 'denied'
            ? "Running in simulation mode for demo purposes. Switch to live mode to use your camera."
            : "Camera is not available. Running in simulation mode only."}
        </p>
      )}
    </Card>
  );
}
